import React, { useState, useEffect } from "react";
import styles from "./styles/ReviewContainer.module.css";

const ReviewContainer = (props) => {
  const [Loading, setLoading] = useState(false);
  useEffect(() => {
    import(`./js/${props.image}.js`).then((e) => {
      setLoading(e.default);
    });
  }, [props.image]);
  return (
    <div className={styles.wrapper}>
      <p className={styles.step}>Step {props.step}</p>
      <div className={styles.imgBackground}>
        <div className={styles.image}>{Loading ? <Loading /> : null}</div>
      </div>
      <p className={styles.content}>{props.name}</p>
    </div>
  );
};

export default ReviewContainer;
