import React from "react";
import styles from "./styles/Content.module.css";
import ReviewContainer from "./ReviewContainer";

const Content = ({ data }) => {
  return (
    <div className={styles.wrapper}>
      <div className={styles.section}>
        <div className={styles.background}></div>
        <div className={styles.contentWrapper}>
          {data.title.split(" ").map((ele) => (
            <h1 className={styles.title}>{ele}</h1>
          ))}
          <h1 className={styles.day}>
            Day
            <br />
            {data.day}
          </h1>
        </div>
      </div>
      <div className={styles.section}>
        <div className={styles.header}>
          <h1 className={styles.heading}> Today'topic</h1>
          <h3 className={styles.subheading}>{data.about}</h3>
        </div>
        <ul className={styles.list}>
          {data.topic.map((ele) => (
            <li>{ele}</li>
          ))}
        </ul>
      </div>
      {data.content.map((section) => (
        <div className={styles.section}>
          <div className={styles.header}>
            <h1 className={styles.heading}>{section.heading}</h1>
          </div>
          {section.content.map((content) => (
            <p className={styles.paragraph}>{content}</p>
          ))}
        </div>
      ))}
      <div className={styles.section}>
        <h1 className={styles.reviewHeading}>Review</h1>
        <p className={styles.paragraph}>{data.review}</p>
        {data.reviewOption.map((ele, index) => (
          <ReviewContainer
            step={index + 1}
            name={ele.title}
            image={ele.image}
          />
        ))}
      </div>
    </div>
  );
};

export default Content;
